<template>
<v-container>
    <v-card-text align="center"><font size="4"><b>-</b></font></v-card-text>
     <v-flex xs10 offset-xs1>
     <v-text-field v-model="title" label="Email" class="input-group--focused"></v-text-field>
    </v-flex>
    <v-flex xs10 offset-xs1>
     <v-text-field v-model="sendEmail" label="" class="input-group--focused"></v-text-field>
    </v-flex>
     <v-flex xs10 offset-xs1>
     <v-text-field v-model="recieveEamil" label="" hint="," class="input-group--focused"></v-text-field>
    </v-flex>
    <v-flex offset-xs4>
        <v-btn @click="save"></v-btn>
    </v-flex>
</v-container>
</template>

<script>
import api from '../store/api'
export default {
data () {
      return {
          sendEmail:'',
          recieveEmail:'',
          title:'',

      }
    },
    methods:{
        save(){
            api.recommendSet(this.data).then(res=>{
                alert('')
            }).catch(error=>{
                alert(error)
            })
        }
    },
    beforeMount(){
        api.recommendGet().then(res=>{
            this.data=res.data.setting;
        }).catch(error=>{
            alert(error);
        })
    }
}
</script>

<style>

</style>



